<template>
<div class="scrollBox">
  <header-nav v-if="$utils.showNavBar()" ref="headerEL"></header-nav>
  <div id="activity" :class="{hasHeader: $utils.showNavBar()}">
    <div class="scrollEl" ref="scrollEl">
      <!-- <div class="banner iconSize">
        <img src="https://jjlmobile.oss-cn-shenzhen.aliyuncs.com/vsn1_1/activity-banner.png" alt="banner">
      </div> -->
      <div class="user">
        <div class="avatar iconSize">
          <img :src="commonState.userInfo.avatar" alt="头像">
        </div>
        <div class="nickname">{{$utils.splitStr(commonState.userInfo.nickname, 8)}}</div>
      </div>
      <div class="rect"></div>
      <div class="activity-tabbar border-1px">
        <div class="item" :class="{active: activeIndex === index}" @click="setActive(index)" v-for="(item, index) in tabList" :key="index">
          {{item.name}}
        </div>
      </div>
      <div class="loadList" v-infinite-scroll="loadMore" infinite-scroll-distance="200">
        <div class="list">
          <div class="list-item" @click="toDetail(index)" v-for="(item, index) in list" :key="index">
            <div class="list-img iconSize">
              <img :src="item.img_url" alt="banner">
            </div>
            <div class="list-content" :class="{isEnd: item.status === 0}">
              <div class="list-left">
                <div class="item-name">{{item.activity_name}}</div>
                <div class="create_time">截止时间：{{item.end_time}}</div>
              </div>
              <div class="flex-row">
                <div class="rightArrowEl" :class="{isAndroid: $utils.isAndroid()}">
                  <div class="text" v-if="item.state === 1">已提交，审核中</div>
                  <div class="text" v-else-if="item.state === 2">审核通过</div>
                  <div class="text" v-else-if="item.state === 3">审核未通过</div>
                  <div class="text" v-else>领取奖励</div>
                  <div class="arrowEl"></div>
                </div>
              </div>
            </div>
          </div>
          <div class="no-list-data" v-if="list.length > 0">没有更多活动了！</div>
          <div class="no-list-data no-data" v-if="list.length === 0">没有更多活动了！</div>
        </div>
      </div>
    </div>
  </div>
</div>
</template>
<script>
import HeaderNav from '@/components/task/header-nav'
import {mapState} from 'vuex'
import ListLoading from '@/components/movie/common/list-loading'
export default {
  computed: mapState({
    commonState: state => state.common
  }),
  components: {
    HeaderNav,
    ListLoading
  },
  data () {
    return {
      tabList: [{
        name: '正在进行',
        status: 1
      }, {
        name: '已结束',
        status: 0
      }],
      page: 1,
      activeIndex: 0,
      list: []
    }
  },
  methods: {
    loadMore () {
      if (this.loading > 0) {
        return
      }
      this.getList(res => {
        if (res.data.data) {
          this.list = [...this.list, ...res.data.data]
        }
      })
    },
    toDetail (index) {
      if (this.tabList[this.activeIndex].status === 1) {
        console.log()
        this.$router.push('/activity_detail/' + this.list[index].id + '?endTime=' + this.list[index].end_time)
      } else {
        this.$toast({
          message: '活动已结束'
        })
      }
    },
    setActive (index) {
      this.page = 1
      this.activeIndex = index
      this.loading = 1
      this.getList(res => {
        this.list = res.data
      })
    },
    getList (cb) {
      let data = {
        page: this.page,
        status: this.tabList[this.activeIndex].status
      }
      this.$api.get('ACTIVITY_LIST', data, res => {
        this.loading = 0
        if (this.page >= res.data.last_page) {
          this.loading = 2
        }
        typeof cb === 'function' && cb(res)
      })
    }
  },
  mounted () {
    if (!this.commonState.login_bol) {
      return this.$router.go(-1)
    }
    this.loading = 1
    this.getList(res => {
      this.list = res.data
    })
  }
}
</script>

<style lang="scss" scoped>
#activity {
  height: 100vh;
  box-sizing: border-box;
  -webkit-overflow-scrolling: touch;
  overflow-y: auto;
  &.Router {
    transition: all 0s!important;
  }
  .user {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    padding: 40px 0 34px;
    .avatar {
      width: 100px;
      height: 100px;
      overflow: hidden;
      border-radius: 50%;
      margin-bottom: 20px;
    }
    font-size: 28px;
    color: #666666;
    .tag {
      margin-top: -6px;
      height: 43px;
      line-height: 43px;
      width: 84px;
      text-align: center;
      margin-left: 20px;
      border: 1px solid #979797;
      border-radius: 8px;
    }
  }
  .activity-tabbar {
    display: flex;
    flex-flow: row nowrap;
    justify-content: center;
    .item {
      width: 300px;
      text-align: center;
      padding: 30px 0 18px;
      font-weight: 900;
      font-size: 32px;
      line-height: 45px;
      position: relative;
      &.active {
        color: #F4A82B;
        &::after {
          content: '';
          position: absolute;
          left: 50%;
          transform: translateX(-50%);
          width: 120px;
          height: 4px;
          right: 0;
          bottom: 0;
          background: #F4A82B;
        }
      }
    }
  }
  .rect {
    height: 20px;
    background: #f5f5f5;
  }
  img {
    display: block;
  }
  .list {
    padding: 20px 18px 100px;
    .list-item {
      background: #FFFFFF;
      box-shadow: 4px 4px 14px 0 #DADADA;
      border-radius: 2px;
      margin-bottom: 30px;
      .list-content {
        padding: 0 11px 22px;
        display: flex;
        flex-flow: row nowrap;
        .list-img {
          width: 714px;
          height: 240px;
        }
        .item-name {
          font-size: 30px;
          height: 30px;
          line-height: 32px;
          color: #333;
          padding: 30px 0 10px;
        }
         .create_time {
          font-size: 26px;
          line-height: 38px;
          height: 38px;
          color: #666;
        }
        .flex-row {
          flex: 1;
          justify-content: flex-end;
          padding-right: 10px;
          align-items: center;
          font-size: 26px;
          color: #4A90E2;
          .rightArrowEl {
            height: 26px;
            display: flex;
            flex-flow: row nowrap;
            justify-content: flex-end;
            font-size: 26px;
            margin-top: 10px;
            align-items: center;
            line-height: 1;
            margin-bottom: -20px;
            &.isAndroid {
              .arrowEl {
                margin-top: -6px;
                width: 14px;
              }
            }
            .arrowEl {
              width: 12px;
              height: 12px;
              border-color: #4A90E2;
            }
          }
        }
        &.isEnd {
          opacity: .3;
          .flex-row {
            color: #666;
            .arrowEl {
              border-color: #666;
            }
          }
        }
      }
    }
  }
  .no-list-data {
      margin-top: 20px;
  }
  .no-data {
    padding: 50px 0;
  }
}
</style>



